import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { svgIcons } from './pushpinIcons.js'

var map = {},
		Microsoft,
		scriptURL = "https://www.bing.com/api/maps/mapcontrol?callback=bingmapsCallback",
		pendingProps = [];

class BingMaps extends Component {

	constructor(props) {
		super(props);
		this.state = {
			// only used if setProps fails, won't trigger dash renderer
			selected: props.selected_state
		}
		if(document.querySelector('script[src="' + scriptURL + '"]') === null) {
			this.loadScript(scriptURL);
			window.bingmapsCallback = function() {
				Microsoft = window.Microsoft;
				this.afterDependencyLoad(pendingProps);
			}.bind(this);
		}
	}

	componentDidMount() {
		if(Microsoft === null || Microsoft === undefined) {
			pendingProps.push(this.props);
		} else {
			this.initMap(this.props, Microsoft);
		}
	}

	afterDependencyLoad(pendingProps) {
		try {
			pendingProps.map((props) => this.initMap(props, Microsoft));
		}
		catch(exception) {
			console.log("Error loading Microsoft bingmaps");
		}
	}

	componentWillUnmount() {
		try {
			let mapReference = this.props.id;
			if(map[mapReference])
				//map[mapReference].dispose();

			map[mapReference] = undefined;
		}
		catch(exception) {
			console.log(exception);
		}
	}

	loadScript(url) {
	    var script = document.createElement("script")
	    script.type = "text/javascript";
	    script.async = true;
	    script.defer = true;
	    script.src = url;
	    document.getElementsByTagName("head")[0].appendChild(script);
	}

	componentWillReceiveProps(nextProps) {
		let mapReference = '#' + nextProps.id;
		if(this.props.center.join() !== nextProps.center.join()) this.setMapCenter(nextProps.center, mapReference)
		if(this.props.directions !== nextProps.directions) this.setDirections(nextProps.directions, mapReference)
		if(this.props.mapTypeId !== nextProps.mapTypeId) this.setMapTypeId(nextProps.mapTypeId, nextProps.center, nextProps.heading, mapReference)
		if(this.props.navigationBar !== nextProps.navigationBar) this.setMapNavigationBar(nextProps.navigationBar, mapReference)
		if(this.props.polygons !== nextProps.polygons) this.setPolygons(nextProps.polygons, mapReference)
		if(this.props.polylines !== nextProps.polylines) this.setPolyline(nextProps.polylines, mapReference)
		if((this.props.pushpins !== nextProps.pushpins)&&(this.props.infoboxOptions !== nextProps.infoboxOptions)) this.setPushpins(nextProps.pushpins, nextProps.infoboxOptions, mapReference)
		if(this.props.zoom !== nextProps.zoom) this.setMapZoom(nextProps.zoom, mapReference)
	}

	isColor(str) {
		var s = new Option().style;
		s.color = str;
		return s.color !== '';
	}

	initMap(props,  Microsoft) {
		const { bingKey, center, directions, infoboxOptions, mapTypeId, navigationBar,
						polygons, polylines, pushpins, setProps, zoom } = props;
		if(bingKey && Microsoft) {
			let mapReference = '#' + props.id;
			map[mapReference] = new Microsoft.Maps.Map(mapReference, {
				credentials: bingKey
			});
			this.setDirections(directions, mapReference);
			this.setMapCenter(center, mapReference);
			this.setMapNavigationBar(navigationBar, mapReference);
			this.setMapTypeId(mapTypeId, mapReference);
			this.setMapZoom(zoom, mapReference);
			this.setPolygons(polygons, mapReference);
			this.setPolyline(polylines, mapReference);
			this.setPushpins(pushpins, infoboxOptions, mapReference, setProps);
		}
	}

	// MakeCallback(callback, data, mapReference) {
	// 	data ? callback(data) : callback();
	// }

	// Include maybe later on instead of definitions in setPushpins()
	// setInfobox(pushpin, infobox, options) {
	// 	if(pushpin && infobox) {
	// 		this.setState({ selected: pushpin.metadata.id });
	// 	}
	// }

	setDirections(directions, mapReference){
		if(map[mapReference] && directions){

			var inputPanel = directions.inputPanel ? directions.inputPanel : null,
					renderOptions =  directions.renderOptions ? directions.renderOptions : null,
					requestOptions = directions.requestOptions ? directions.requestOptions : null,
					wayPoints = directions.wayPoints ? directions.wayPoints : [];

			Microsoft.Maps.loadModule('Microsoft.Maps.Directions', function() {

				for(let hdx = 0; hdx < wayPoints.length; hdx++) {
					var wayPointsAsDirection = [];
					// Create instance of the directions manager
					var directionsManager = new Microsoft.Maps.Directions.DirectionsManager(map[mapReference]);
					directionsManager.clearAll();
					// Display the input panel
					if(inputPanel) directionsManager.showInputPanel(inputPanel)
					// Specify the render options
					if(renderOptions) {
						// Set a container which displays the intinerary
						if(renderOptions.itineraryContainer) renderOptions.itineraryContainer = document.getElementById(renderOptions.itineraryContainer)
						// Edit the route line style with renderOptions.drivingPolylineOptions
						// Edit the waypoint style with renderOptions.waypointPushpinOptions
						// Disable auto zoom to lates route at init
						renderOptions.autoUpdateMapView = renderOptions.autoUpdateMapView || false
						// Set the render options
						directionsManager.setRenderOptions(renderOptions);
					}
					// disable route dragging
					directionsManager.setRequestOptions({
            routeDraggable: false
					})
					// Set route mode to transit
					if(requestOptions) {
						let distanceUnit = requestOptions.distanceUnit ? Microsoft.Maps.Directions.DistanceUnit[requestOptions.distanceUnit] : null;
						// route mode is driving
						let routeMode = requestOptions.routeMode ? Microsoft.Maps.Directions.RouteMode[requestOptions.routeMode] : null;
						let routeAvoidance = requestOptions.routeAvoidance ? Microsoft.Maps.Directions.RouteAvoidance[requestOptions.routeAvoidance] : null;
						let routeOptimization = requestOptions.routeOptimization ? Microsoft.Maps.Directions.RouteOptimization[requestOptions.routeOptimization] : null;
						let timeType = requestOptions.timeType ? Microsoft.Maps.Directions.TimeTypes[requestOptions.timeType] : null;

						let vehicleSpec = requestOptions.vehicleSpec || null;
						let maxRoutes = requestOptions.maxRoutes || null;
						// disable dragging
						let routeDraggable = requestOptions.routeDraggable || null;
						let routeIndex = requestOptions.routeIndex || null;
						let time = requestOptions.time || null;

						directionsManager.setRequestOptions(Object.assign({},
							distanceUnit && {distanceUnit},
							maxRoutes && {maxRoutes},
							routeMode && {routeMode},
							routeAvoidance && {routeAvoidance},
							routeOptimization && {routeOptimization},
							timeType && {timeType},
							vehicleSpec && {vehicleSpec},
							routeDraggable && {routeDraggable},
							routeIndex && {routeIndex},
							time && {time}
						));
					}
					// Create the waypoints
					for(var idx = 0; idx < wayPoints[hdx].length; idx++) {
						// If address
						let address = wayPoints[hdx][idx].address ? wayPoints[hdx][idx].address : null
						// If location
						let location = wayPoints[hdx][idx].location
										&& wayPoints[hdx][idx].location[0]
										&& wayPoints[hdx][idx].location[1]
										? new Microsoft.Maps.Location(wayPoints[hdx][idx].location[0], wayPoints[hdx][idx].location[1])
										: null;
						// True, if this is only a pass trhough point
						let isViaPoint = wayPoints[hdx][idx].isViaPoint ? wayPoints[hdx][idx].isViaPoint : null;
						// Add to waypoint list
						wayPointsAsDirection.push(new Microsoft.Maps.Directions.Waypoint({ address, location, isViaPoint }));
					}
					// Only calculate direction if waypoints are valid
					if(wayPointsAsDirection.length !== 0) {
						for(var jdx = 0; jdx < wayPointsAsDirection.length; jdx++){
							directionsManager.addWaypoint(wayPointsAsDirection[jdx]);
						}
						directionsManager.calculateDirections();
					}
				}

			});

		}
	} // --- END - setDirections() -----------------------------------------------

	setMapCenter(center, mapReference) {
		if(map[mapReference] && center && center[0] && center[1]) {
			map[mapReference].setView({
				center: new Microsoft.Maps.Location(center[0], center[1])
	    });
		}
	}

	setMapNavigationBar(navigationBar, mapReference){
		if(map[mapReference] && navigationBar && navigationBar.mode) {
			map[mapReference].setOptions({
        navigationBarMode: Microsoft.Maps.NavigationBarMode[navigationBar.mode]
      });
		}
		if(map[mapReference] && navigationBar && navigationBar.orientation && navigationBar.mode!=='compact') {
			map[mapReference].setOptions({
        navigationBarOrientation: Microsoft.Maps.NavigationBarOrientation[navigationBar.orientation]
      });
		}
	}

	setMapTypeId(mapTypeId, mapReference) {
		if(map[mapReference] && mapTypeId) {
			map[mapReference].setView({
				mapTypeId: Microsoft.Maps.MapTypeId[mapTypeId]
	    });
		}
	}

	setMapZoom(zoom, mapReference) {
		if(map[mapReference] && zoom) map[mapReference].setView({ zoom: zoom })
	}

	setPushpins(pushpins, infoboxOptions, mapReference, setProps) {
		if(map[mapReference] && pushpins) {
			// remove existing pushpins
			for(var i = map[mapReference].entities.getLength() - 1; i >= 0; i--) {
	      var pushpin = map[mapReference].entities.get(i);
	      if(pushpin instanceof Microsoft.Maps.Pushpin) map[mapReference].entities.removeAt(i)
		  }
			// create hidden reusible infobox at the center (don't have to render one for each pushpin)
			var infobox = new Microsoft.Maps.Infobox(map[mapReference].getCenter(), { visible: false });
			infobox.setMap(map[mapReference]);
			// styling of infobox
			infoboxOptions = infoboxOptions || {}
			infoboxOptions.template_rw = '<div style="background-color:white;color:black;max-width:280px;padding:5px;border:1px solid black;border-radius:3px;font-size:12px;">' +
																		'<div style="font-weight:bold;border-bottom:1px solid black;padding-bottom:2px;">{location}</div>' +
																		'<div style="padding-bottom:2px;">{description}</div>' +
																		'<div style="border-top:1px solid black;">{duration}</div>' +
																	'</div>';
			infoboxOptions.template = '<div style="background-color:white;color:black;max-width:280px;padding:5px;border:1px solid black;border-radius:3px;font-size:12px;">' +
																	'<div style="font-weight:bold;border-bottom:1px solid black;padding-bottom:2px;">{title}</div>' +
																	'<div style="padding-bottom:2px;">{description}</div>' +
																'</div>';
			// add pushpins
			for(var idx = 0; idx < pushpins.length; idx++) {
					if(pushpins[idx].location && pushpins[idx].location[0] && pushpins[idx].location[1]) {
					let location =  new Microsoft.Maps.Location(pushpins[idx].location[0], pushpins[idx].location[1]);
					// add options to pushpin
					let options = pushpins[idx].options ? pushpins[idx].options : null
					if(options && options.icon) {
						if(Object.keys(svgIcons).includes(options.icon)) {
							options.icon = svgIcons[options.icon]
						} else {
							// TODO: Build filter for svg and png input
							// options.icon = undefined;
							// console.error("Could not find pushpin icon '" + options.icon + "'.");
						}
					}
					if(options && options.anchor && options.anchor[0] && options.anchor[1]) {
						options.anchor = new Microsoft.Maps.Point(options.anchor[0], options.anchor[1])
					}
					if(options && options.color) {
						if(!this.isColor(options.color)) options.color = undefined
					}

					let pushpin = new Microsoft.Maps.Pushpin(location, options);
					map[mapReference].entities.push(pushpin);
					// if(pushpins[idx].addHandler) {
					// 	Microsoft.Maps.Events.addHandler(pushpin, pushpins[idx].addHandler.type,
					// 		function (callback, data) {
					// 			this.MakeCallback(callback, data)
					// 		}.bind(this, pushpins[idx].addHandler.callback, pushpins[idx].addHandler.callbackData));
					// }
					// Microsoft.Maps.Events.addHandler(pushpin, "click", this.setInfobox(pushpins[idx], infobox, infoboxOptions));
					Microsoft.Maps.Events.addHandler(pushpin, "mouseover",
						function (pushpin, pushpinOptions, infobox, infoboxOptions) {
							if(pushpinOptions.metadata && pushpinOptions.metadata.address && pushpinOptions.metadata.measure && pushpinOptions.metadata.start && pushpinOptions.metadata.end) {
								var pushpinLocation = pushpin.getLocation(),
										data = pushpinOptions.metadata,
										address = data.address,
										description = data.measure,
										duration = data.start + ' - ' + data.end;
								infobox.setOptions({
									closeDelayTime: 200,
									location: new Microsoft.Maps.Location(pushpinLocation.latitude, pushpinLocation.longitude),
									offset: new Microsoft.Maps.Point(15, 30),
									showCloseButton: false,
									showPointer: false,
									visible: true,
									htmlContent: infoboxOptions.template_rw.replace('{location}', address).replace('{description}', description).replace('{duration}', duration)
								})
							} else if(pushpinOptions.metadata && pushpinOptions.metadata.title && pushpinOptions.metadata.description) {
								var pushpinLocation = pushpin.getLocation(),
										data = pushpinOptions.metadata,
										title = data.title,
										description = data.description;
								infobox.setOptions({
									closeDelayTime: 200,
									location: new Microsoft.Maps.Location(pushpinLocation.latitude, pushpinLocation.longitude),
									offset: new Microsoft.Maps.Point(15, 30),
									showCloseButton: false,
									showPointer: false,
									visible: true,
									htmlContent: infoboxOptions.template.replace('{title}', title).replace('{description}', description)
								})
							} else if(pushpinOptions.metadata && pushpinOptions.metadata.title && pushpinOptions.metadata.outlets) {
								var pushpinLocation = pushpin.getLocation(),
										data = pushpinOptions.metadata,
										title = data.title,
										description = data.address;
								infobox.setOptions({
									closeDelayTime: 200,
									location: new Microsoft.Maps.Location(pushpinLocation.latitude, pushpinLocation.longitude),
									offset: new Microsoft.Maps.Point(15, 30),
									showCloseButton: false,
									showPointer: false,
									visible: true,
									htmlContent: infoboxOptions.template.replace('{title}', title).replace('{description}', description)
								})
							}
						}.bind(this, pushpin, pushpins[idx], infobox, infoboxOptions));
						Microsoft.Maps.Events.addHandler(pushpin, "mouseout",
							function (infobox) {
								infobox.setOptions({visible: false})
							}.bind(this, infobox));
						Microsoft.Maps.Events.addHandler(pushpin, "click",
							function (pushpinOptions, setProps) {
								if(setProps) {
									setProps({selected: pushpinOptions.metadata.id})
								} else {
									this.setState({selected_state: pushpinOptions.metadata.id});
								}
							}.bind(this, pushpins[idx], setProps));
				} else {
					console.error("Did not provide a location for pushpin: " + pushpin[idx])
				}
			}
		}
	} // --- END - setPushpins() -------------------------------------------------

	setPolygons(polygons, mapReference){
		if(map[mapReference] && polygons){

			for(var i = map[mapReference].entities.getLength() - 1; i >= 0; i--) {
        var regularPolygon = map[mapReference].entities.get(i);
        if (regularPolygon instanceof Microsoft.Maps.Polygon) map[mapReference].entities.removeAt(i)
		  }

	    for(let idx = 0; idx < polygons.length; idx++){
				if(polygons[idx].method==='simple') {
					if(polygons[idx].center && polygons[idx].center[0] && polygons[idx].center[1]) {
		    		let location =  new Microsoft.Maps.Location(polygons[idx].center[0], polygons[idx].center[1]);
		    		let radius = polygons[idx].radius ? polygons[idx].radius : 0;
		    		let points = polygons[idx].points ? polygons[idx].points : 0;
		    		let options = polygons[idx].options ? polygons[idx].options : {};
		    		Microsoft.Maps.loadModule('Microsoft.Maps.SpatialMath', function () {
					    var locations = Microsoft.Maps.SpatialMath.getRegularPolygon(location, radius, points, Microsoft.Maps.SpatialMath.DistanceUnits.Miles);
					    var polygon = new Microsoft.Maps.Polygon(locations, options);
					    map[mapReference].entities.push(polygon);
						});
					}
				} else if(polygons[idx].method==='complex') {
						var polyShape = [];
						for(let jdx = 0; jdx < polygons[idx].points.length; jdx++) {
								var point = polygons[idx].points[jdx];
								if(point[0] && point[1]) {
										polyShape.push(new Microsoft.Maps.Location(point[0], point[1]));
								} else {
									console.error("Cannot read point: " + point);
								}
						}
						var options = polygons[idx].options || {} ;
						var polygon = new Microsoft.Maps.Polygon(polyShape, options);
						map[mapReference].entities.push(polygon);
				} else {
					console.error("No method defined for Polygon object" + polygons[idx])
				}
	    }

		}
	} // --- END - setPolygons() -------------------------------------------------

	setPolyline(polylines, mapReference){
		if(map[mapReference] && polylines){
			// remove previous one
			for(var i = map[mapReference].entities.getLength() - 1; i >= 0; i--) {
	      var ref = map[mapReference].entities.get(i);
	      if(ref instanceof Microsoft.Maps.Polyline) map[mapReference].entities.removeAt(i)
			}

			// Add every polyline (should be optimized)
			for(let idx = 0; idx < polylines.length; idx++) {
				var options = polylines[idx].options || null,
						line = polylines[idx].points || null,
						apiFormatLocations = [];
				for(var jdx = 0; jdx < line.length && line[jdx][0] && line[jdx][1]; jdx++ ) {
					apiFormatLocations.push(new Microsoft.Maps.Location(line[jdx][0], line[jdx][1]))
				}
				if(apiFormatLocations.length !== 0) {
					var polylineObject = new Microsoft.Maps.Polyline(apiFormatLocations, options);
					map[mapReference].entities.push(polylineObject);
				}
			}

		}
	} // --- END - setPolyline() -------------------------------------------------

	render() {
		return(
			<div
					id = {this.props.id}
					className = { this.props.className }
			></div>
		);
	}
};

export default BingMaps;

BingMaps.defaultProps = {
	bingKey: 'AlXL42w92coK1623DVOrVzA1Gf-pUQbKb641JzTw_47B_sBpXowhGfvDbN6P70IE',
	center: [48.800050, 9.797662],
	directions: undefined,
	id: undefined,
	mapTypeId: 'canvasLight',
	navigationBar: {
		mode: 'minified',
		orientation: 'horizontal'
	},
	polygons: undefined,
	polylines: undefined,
	pushpins: undefined,
	selected: undefined,
	zoom: 14,
};

BingMaps.propTypes = {
	/**
	* API Key for Bing Maps V8 Web Control
	*/
	bingKey: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	/**
	* Array containing latitude and longitude of the map center
	*/
	center: PropTypes.arrayOf(PropTypes.number),
	/**
	* Directions (needs better proptype protection)
	*/
	directions: PropTypes.object,
	/**
	* Id to which map DOM elements will be bound to as children
	*/
  id: PropTypes.string.isRequired,
	/**
	* infoboxOptions
	*/
  infoboxOptions: PropTypes.object,
	/**
	* Map type
	* https://docs.microsoft.com/en-us/bingmaps/v8-web-control/map-control-api/maptypeid-enumeration
	*/
	mapTypeId: PropTypes.string,
	/**
	* Navigation bar display configuration
	* https://docs.microsoft.com/en-us/bingmaps/v8-web-control/map-control-api/navigationbarmode-enumeration
	*/
	navigationBar: PropTypes.shape({
		mode: PropTypes.oneOf(['compact', 'default', 'minified']),
		orientation: PropTypes.oneOf(['horizontal', 'vertical'])
	}),
	/**
	* Polgons - either simple (creating points automatically) or complex (needs point input)
	*/
	polygons: PropTypes.arrayOf(
			PropTypes.oneOfType([
					PropTypes.shape({
							method: 'simple',
							center: PropTypes.arrayOf(PropTypes.number),
							radius: PropTypes.number,
							points: PropTypes.number,
							options: PropTypes.object
					}),
					PropTypes.shape({
							method: 'complex',
							points: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.number)),
							options: PropTypes.object
					})
			])
	),
	/**
	* Polylines from point A to B (needs better proptype protection)
	*/
	polylines: PropTypes.array,
	/**
	* Pushpins to be added to map
	*/
	pushpins: PropTypes.arrayOf(
		PropTypes.shape({
	    location: PropTypes.arrayOf(PropTypes.number),
	    options: PropTypes.object,
			metadata: PropTypes.object
		})
	),
	/**
	* Dash-assigned callback that informs the dash renderer that this component's
	* properties have changed
	*/
	setProps: PropTypes.func,
	/**
	* Id of selected object in component (only pushpins for now)
	*/
	selected: PropTypes.string,
	/**
	* Zoom level
	*/
	zoom: PropTypes.number,
};
